.swiper-container {
  position: relative;
  padding: 0;

  @include vp-1199 {
    padding: 0 55px;
  }

  .swiper-container__button-next,
  .swiper-container__button-prev {
    width: 16px;
    height: 32px;
    top: 52%;

    @include vp-1199 {
      top: 52%;
    }

    @include vp-767 {
      width: 10px;
      height: 19px;
      top: 54%;
    }
  }

  .swiper-container__button-next {
    right: -56px;

    @include vp-1199 {
      right: 0;
    }
  }

  .swiper-container__button-prev {
    left: -56px;

    @include vp-1199 {
      left: 0;
    }
  }
}
